@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100..900&family=Rubik+Glitch&display=swap");
//reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  overflow-x: hidden;
}
.container {
  font-family: "Noto Sans TC", sans-serif;
  width: 80%;
  margin: 0 auto;
  @media (max-width: 768px) {
    width: 90%;
  }
}
//nav
nav {
  position: fixed;
  top: 0;
  width: 100%;
  border-bottom: 1px solid #f3f3f3;
  background: #fff;
  z-index: 10;
  .navbar {
    height: 80px;
    display: flex;
    align-items: center;
    gap: 20vw;
    justify-content: space-between;
    @media (max-width: 768px) {
      justify-content: center;
      gap: 10px;
    }
    a {
      font-family: "Rubik Glitch", system-ui;
      font-weight: 400;
      text-decoration: none;
      color: #5397fc;
      display: flex;
      align-items: center;
      span {
        margin-left: 10px;
      }
      @media (max-width: 768px) {
        font-size: 28px;
        img{
          display: none;
        }
      }
    }
  }
}

//footer
footer {
  width: 100%;
  padding: 50px 0;
  border-top: 1px solid #f3f3f3;
  p {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  a{
    margin: 0 5px;
  }
  @media(max-width:768px){
    p{
      flex-direction: column;
    }
   
  }
}
