.home-wrap {
  margin-top: 15vh;
  min-height: 100vh;
}
.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  gap: 2vw;
  margin-bottom: 10vh;
  
  @media (max-width: 768px) {
    margin-bottom: 5vh;
  }
  h2 {
     color: #2c2c2c;
    font-size: 2.5rem;
    margin-bottom: 20px;
    @media (max-width: 768px) {
      font-size: 1.8rem;
    }
  }
  .video-wrap {
    width: 50%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 10px 10px 10px gray;
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    @media (max-width: 1280px) {
      display: none;
    }
  }
}
.pictures-wrap {
  column-count: 4; /* 4欄 */
  gap: 16px; /* 欄位之間的距離 */
  margin: 5vh 0;
  @media (max-width: 1280px) {
    column-count: 3;
  }
  @media (max-width: 768px) {
    column-count: 2;
  }
}

.loading-wrap {
  width: 100%;
  margin: 10vh 0;

  display: grid;
  place-items: center;
}

.morePicture {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10vh 0;
  button {
    width: 150px;
    height: 50px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    background-color: #5397fc;
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
  }
}

.noPicture {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #2c2c2c;
  h3 {
    margin-top: 10vh;
    font-size: 2rem;
  }
  span {
    font-size: 2.5rem;
    color: #5397fc;
    margin: 0 10px;
  }
  p {
    margin-top: 40px;
    font-size: 8rem;
  }
  @media (max-width: 768px) {
    h3 {
      margin-top: 0;
      font-size: 1.8rem;
    }
    span{
      font-size: 1.8rem;
      margin: 0;
    }
    p {
      margin-top: 20px;
      font-size: 6rem;
    }
  }
}
