//搜尋框
.search {
  position: relative;
  width: 100%;
  input {
    width: 100%;
    height: 50px;
    border-radius: 10px;
    border: 1px solid #f5f5f5;
    outline: none;
    background-color: #f3f3f3;
    padding: 0 20px;
    font-size: 20px;
  }
  button {
    position: absolute;
    top: 2px;
    right: 2px;
    width: 60px;
    height: 90%;
    border-radius: 10px;
    border: none;
    background-color: #f5f5f5;
    cursor: pointer;
    font-size: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
      background-color: #ebebeb;
    }
  }
 
}