.picture-card {
  position: relative;
  border-radius: 10px;
  max-width: 400px;
  overflow: hidden;
  cursor: pointer;
  break-inside: avoid; /* 避免圖片被切斷 */
  margin-bottom: 16px; /* 每張圖片下方間距 */
  img {
    transition: 0.5s;
    width: 100%;
    border-radius: 10px;
  }
  .download-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: none;
    background-color: #fff;
    color: #5397fc;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    opacity: 0;
    transition: 0.5s;
    font-size: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      transform: scale(1.2);
    }
    @media(max-width:768px){
      width: 50px;
      height: 50px;
      opacity: 1;
    }
  }
  .info-wrap {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 80px;
    border-radius: 0 0 10px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba($color: #000000, $alpha: 0.3);
    backdrop-filter: blur(5px);
    transform: translateY(150px);
    transition: 0.3s;
    padding: 0 10px;
    a {
      color: #fff;
      font-size: 20px;
      font-weight: bold;
      text-decoration: none;
    }
    @media(max-width:768px){
      padding: 0 8px;
      transform: translateY(0);
      a{
        font-size: 16px;
      }
    }
  }
  &:hover {
    img {
      transform: scale(1.2);
    }
    .download-btn {
      opacity: 1;
    }
    .info-wrap {
      transform: translateY(0);
    }
  }
}

.fullPicture {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1000;
  overflow: hidden;
  display: grid;
  place-items: center;
  opacity: 0;
  transition: 0.3s;
  .img-wrap {
    position: relative;
    animation: scaleIn 0.3s ease;
    img {
      border-radius: 10px;
      max-width: 100%;
      max-height: 90vh;
     
    }
  }

  //動畫效果
  &.show {
    opacity: 1;
  }
  &.hide {
    opacity: 0;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
